<template>
  <div class="notediscussionprint">
    <!-- 畫面顯示內容區域 -->
    <h3>{{ cData.company_name }}</h3>

    <div class="text-center border-bottom my-2">
      <h1>
        <strong>跨領域團隊合作照會討論紀錄表</strong>
      </h1>
    </div>
    <v-form>
      <table
        border="2px solid #00"
        class="form_title text-nowrap"
        style="width: 100%"
      >
        <tr>
          <td style="width: 25%">
            <span class="subTitle">討論時間</span>
          </td>
          <td>
            日期: {{ list.date }} <br />
            時間: {{ list.timeStart }} ~ {{ list.timeEnd }}
          </td>
          <td>
            主席: {{ list.chairman }} <br />
            紀錄: {{ list.recorder }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">團隊成員</span>
          </td>
          <td colspan="2" class="pb-3">
            <div
              class="d-flex align-end"
              v-for="(teamMember, index) in teamMembers"
              :key="index"
            >
              <v-checkbox
                readonly
                :label="teamMember.label"
                class="mr-2"
                v-model="list[teamMember.columnName]"
                hide-details
                v-if="list[teamMember.columnName]"
              ></v-checkbox>
              <div v-if="list[teamMember.columnName]">
                : {{ list[teamMember.columnName] }}
              </div>
            </div>
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">討論議題</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.discussionTopics }}
          </td>
        </tr>

        <tr>
          <td class="pa-3">
            <span class="subTitle d-block mb-4">基本資料</span>
          </td>
          <td colspan="2" class="pa-3">
            <v-row
              style="font-size: 1.5rem; line-height: 0.5"
              v-if="this.list.user"
            >
              <v-col cols="3">姓名: {{ this.list.user.name }} </v-col>
              <v-col cols="3">性別: {{ this.list.user.sex }}</v-col>
              <v-col cols="3">床號: {{ this.list.user.bed_name }}</v-col>
              <v-col cols="3"
                >年齡: {{ showAge(this.list.user.birthday) }}</v-col
              >

              <v-col cols="4">入住日期: {{ this.list.user.in_date }}</v-col>

              <v-col cols="4"
                >學歷:
                <span v-if="nursingassessment">{{
                  showEducation()
                }}</span></v-col
              >
              <v-col cols="4">婚姻狀況: {{ showMarry(this.list.user) }}</v-col>

              <v-col cols="4">入住來源: {{ this.list.user.source }}</v-col>
              <v-col cols="12"
                >入住診斷: {{ showDiagnosis(this.list.user) }}
              </v-col>
              <v-col cols="12"
                >經濟狀況:
                <span v-if="treatment">{{
                  this.treatment.economicsources
                }}</span>
              </v-col>
              <v-col cols="12"
                >家系圖:
                <v-img
                  :src="
                    $store.state.base_url +
                    'upload/familytree/' +
                    list.user.pic_url_familytree
                  "
                  class="mt-2"
                  height="500px"
                  contain
                  v-if="list.user.pic_url_familytree != 'lazypic.jpg'"
                ></v-img>
              </v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td colspan="3" class="text-center">
            <span class="subTitle">討論內容</span>
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">病史及入住過程</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.historyAndProcess }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">目前情形</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.currentSituation }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">管路情形</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.pipelineSituation }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">協助照顧執行目標</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.careGoals }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">社會互動情形</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.socialInteraction }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">護理問題</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.nursingProblem }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">建議措施</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.suggested }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">跨專業討論</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.interdisciplinaryDiscussion }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">評值及追蹤</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.evaluationTracking }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle">結論</span>
          </td>
          <td
            colspan="2"
            class="pa-3"
            style="word-break: break-all; white-space: pre-line"
          >
            {{ list.conclusion }}
          </td>
        </tr>

        <tr>
          <td>
            <span class="subTitle"> 照片(最多3張) </span>
          </td>
          <td colspan="2" class="pa-3">
            <v-row>
              <v-col cols="4" v-for="(pic, index) in list.picName" :key="index">
                <v-img
                  :src="$store.state.base_url + 'upload/notediscussion/' + pic
                  "
                  :lazy-src="
                    $store.state.base_url +
                    'upload/notediscussion/lazypic.jpg'
                  "
                  class="text-right pa-1"
                  max-width="400"
                >
                </v-img>
              </v-col>
            </v-row>
          </td>
        </tr>
      </table>
    </v-form>

    <!-- <pre>{{ list.picName }}}</pre> -->
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      picItems: [],
      //團隊成員
      teamMembers: [
        { label: "醫生", columnName: "doctor" },
        { label: "居家護理師", columnName: "homeNurse" },
        { label: "護理人員", columnName: "nursing" },
        { label: "營養師", columnName: "nutritionist" },
        { label: "社工員", columnName: "socialWorker" },
        { label: "物理治療師", columnName: "physicalTherapist" },
        { label: "藥師", columnName: "pharmacist" },
        { label: "照服員", columnName: "attendant" },
        { label: "家屬", columnName: "familyMembers" },
      ],
      nursingassessment: null,
      treatment: null,
    };
  },

  computed: {
    // 參加成員
    members() {
      return this.list.memberArray.reduce((str, member, index) => {
        if (index != 0) {
          str += ",";
        }
        return (str += member.text);
      }, "");
    },
    userCounts() {
      //參加人數
      var man = this.list.memberArray.filter((item) => {
        return item.sex == "男";
      });
      var women = this.list.memberArray.filter((item) => {
        return item.sex == "女";
      });
      return (
        "總人數:" +
        this.list.memberArray.length +
        "人, 男:" +
        man.length +
        "人, 女:" +
        women.length +
        "人"
      );
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    // this.pData = JSON.parse(sessionStorage.getItem("pData"));
    // this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.list = JSON.parse(this.$route.query.data);
    this.getPic();
  },

  methods: {
    getNursingassessment() {
      if (!this.list.user) {
        return false;
      }
      var postData = {
        key: "u_snkey",
        value: this.list.user.snkey,
      };

      this.$api.options(
        "general/getByKey/" +
          this.$store.state.databaseName +
          "/nursingassessment",
        postData
      ).then((rs) => {
        if (rs.length) {
          this.nursingassessment = JSON.parse(rs[0].datalist);
        }
      });
    },
    getPic() {
      //取得活動圖片
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getPicBySnkey/" +
            this.$store.state.databaseName +
            "/activity/activity/" +
            this.list.snkey
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            this.picItems = rs.data.picArray;
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //取得入院護理評估資料
    getNursingassessment() {
      if (!this.list.user) {
        return false;
      }
      var postData = {
        key: "u_snkey",
        value: this.list.user.snkey,
      };

      this.$api.options(
        "general/getByKey/" +
          this.$store.state.databaseName +
          "/nursingassessment",
        postData
      ).then((rs) => {
        if (rs.length) {
          this.nursingassessment = JSON.parse(rs[0].datalist);
        }
      });
    },
    //取得個案服務處遇計畫表
    getTreatment() {
      if (!this.list.user) {
        return false;
      }
      var postData = {
        key: "u_snkey",
        value: this.list.user.snkey,
      };

      this.$api.options(
        "general/getByKey/" + this.$store.state.databaseName + "/treatment",
        postData
      ).then((rs) => {
        if (rs.length) {
          this.treatment = JSON.parse(rs[0].datalist);
        } else {
          this.treatment = null;
        }
      });
    },
    //回傳年齡
    showAge(birthday) {
      if (birthday == "0000-00-00") {
        return "未設定生日";
      } else {
        return dayjs().format("YYYY") - dayjs(birthday).format("YYYY");
      }
    },
    //回傳婚姻狀況
    showMarry(user) {
      if (user.marry1) {
        return "未婚";
      }
      if (user.marry2) {
        return "已婚";
      }
      if (user.marry3) {
        return "離婚";
      }
      if (user.marry4) {
        return "喪偶";
      }
      if (user.marry5) {
        return "分居";
      }
      if (user.marry6) {
        return "其他:" + user.marry7;
      }
    },
    //回傳教育程度
    showEducation() {
      if (this.nursingassessment.education1) {
        return "不識字";
      }
      if (this.nursingassessment.education2) {
        return "國小以下";
      }
      if (this.nursingassessment.education3) {
        return "小學";
      }
      if (this.nursingassessment.education4) {
        return "國中";
      }
      if (this.nursingassessment.education5) {
        return "高中(職)";
      }
      if (this.nursingassessment.education6) {
        return "大學/大專";
      }
      if (this.nursingassessment.education7) {
        return "大學以上";
      }
      if (this.nursingassessment.education8) {
        return "不確定";
      }
    },
    //回傳入住診斷
    showDiagnosis(user) {
      // console.log('user',user.diagnosis)
      let newObj = Object.values(user.diagnosis);
      // console.log('newObj',newObj)
      return newObj.reduce((str, item, index) => {
        return (str += `(${index + 1})${item} `);
      }, "");
    },
    //取得個案服務處遇計畫表
    getTreatment() {
      if (!this.list.user) {
        return false;
      }
      var postData = {
        key: "u_snkey",
        value: this.list.user.snkey,
      };

      this.$api.options(
        "general/getByKey/" + this.$store.state.databaseName + "/treatment",
        postData
      ).then((rs) => {
        if (rs.length) {
          this.treatment = JSON.parse(rs[0].datalist);
        } else {
          this.treatment = null;
        }
      });
    },
  },
};
</script>

<style lang="scss">
.notediscussionprint {
  @import "node_modules/bootstrap/scss/bootstrap";
  @import "node_modules/bootstrap-vue/src/index.scss";

  .form_title {
    font-size: 1.6rem;
    // text-align: center;
    font-family: "微軟正黑體";
    td {
      padding: 5px;
    }
  }

  .v-label {
    font-size: 1.6rem;
  }
  // .vertical-mode {
  //   writing-mode: tb-rl;
  //   -webkit-writing-mode: vertical-rl;
  //   writing-mode: vertical-rl;
  // }
}
</style>